
:root {
    --primary: #003060;
}
  
.btn {
    padding: 8px 20 px;
    border-radius : 4px;
    outline : none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 8px 20 px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #6568F4;
    transition: 250ms;
}

.btn--primary {
    background-color: var(--primary) ;

}

.btn--outline {
    background-color : transparent;
    color : #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size :12px;
    color:white;

}



.btn--large {
    padding: 12px 20px;
    border-radius: 4px;
    font-size :20px;
    color:white;

}