.dropdown-menu {
  background:white;
  width: 210px;
  position: absolute;
  top: 60px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: white;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background:white;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}