.error {
    border: 1px solid red;
  }
  
  .error-message {
    color: red;
    font-size: 1.5rem;
  }
  
  .card {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    .log-in {
      margin-top: 4rem;
      h1 {
        font-size: 200%;
        text-align: center;
      }
      h6 {
        text-align: center;
      }
    }
  }
  
  /* On mouse-over, add a deeper shadow */
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
  
  .container {
    padding: 2px 16px;
  }
  .disc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  